*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: rgb(42, 106, 224);
}

.main-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.center-container{
  height: 500px;
  width: 350px;
  background-color: black;
  padding: 20px;
  border-radius: 20px;
}

.app-heading{
  padding: 4px; 
  color: white;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
}

.input-box-todo{
  width: 220px;
  padding: 15px;
  color: rgb(255, 255, 255);
/* order: reverse; */
  font-size: 18px;
  border: 1px solid white;
  border-radius: 10px;
  background-color: rgba(27, 24, 24, 0.895);
}

.add-btn{
  font-size: 30px;
  color: white;
  background-color: rgb(42, 106, 224);
  text-align: center;
  border: none;
  height: 40px;
  width: 40px;
  margin-left: 6px;
  border-radius: 2px;
}

.list-item{
  list-style: none;
  color: white;
  background-color: rgba(27, 24, 24, 0.895);
  border-radius: 10px;
  width: 280px;
  margin-top: 20px;
  padding: 12px;
  display: flex;
  font-size: 17px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  position: relative;
}

.list-item span{
  position: absolute;
  right: 12px;
}

.icon-delete:hover{
  color: rgb(226, 51, 51);
  justify-content: end;
}

hr{
  width: 85%;
}

.add-btn{
  margin-top: 25px;
}

.app-heading{
font-size: 35px;
margin-top: 5px;
font-family:Verdana, Geneva, Tahoma, sans-serif;
}


  /* STYLE 4 */

.scrollbar
{
	margin-left: 0px;
	float: left;
	height: 270px;
	width: 99%;
	overflow-y: scroll;
	margin-bottom: 25px;
	margin-top: 35px;
}

/* .force-overflow{
	min-height: 450px;
} */

#style-4::-webkit-scrollbar-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
 }
 
 #style-4::-webkit-scrollbar
 {
   width: 7px;
   /* background-color: #fff; */
   background-color: #000;
 }
 
 #style-4::-webkit-scrollbar-thumb
 {
   /* background-color: #000; */
   background-color: #fff;
   border: 1px solid #6a6565;
 }
 
 